<template>
	<div>
		<div class="testList">
			<mu-list style="height: 500px; font-size: 10px;" describeTextClass="titleList">
				<template v-for="(title, index) in titleList">
					<mu-list-item :title="index+1 + '. 状态：' + state[index]" @click="changeShow(index)"/>	
				</template>
	      
	    </mu-list>
		</div>
		<div class="testContant">
			<span class="project">
				{{ project }} 
			</span>
			<span class="time">
				时间：还剩 {{ parseInt(time/60) }} 分 {{ time%60 }} 秒
			</span>
			<div class="testTitle">
				<span>{{nowIndex+1}}</span>. <span>{{ testTitle }}</span>
			</div>
			<div class="testOptions">
				<div class="selct" :style="'display:' + selctDisplay">
					<p>A. {{testSelectA}}</p>
					<p>B. {{testSelectB}}</p>
					<p>C. {{testSelectC}}</p>
					<p>D. {{testSelectD}}</p>
					<mu-checkbox name="group" nativeValue="A" v-model="answer" label="A" class="demo-checkbox"/>
			    <mu-checkbox name="group" nativeValue="B" v-model="answer" label="B" class="demo-checkbox"/>
			    <mu-checkbox name="group" nativeValue="C" v-model="answer" label="C" class="demo-checkbox"/>
			    <mu-checkbox name="group" nativeValue="D" v-model="answer" label="D" class="demo-checkbox"/>
			  </div>
			  <div class="judge" :style="'display:' + judgeDisplay">
			  	<mu-radio label="对" name="group" nativeValue="对" v-model="judgeVal" class="demo-radio"/> <br/>
    			<mu-radio label="错" name="group" nativeValue="错" v-model="judgeVal"  class="demo-radio"/> <br/>
			  </div>
			</div>
			<mu-raised-button label="下一题" class="demo-raised-button" style="margin-top: 20px;" primary @click="changeShow(nowIndex+1)"/>
			<mu-raised-button label="交卷" class="demo-raised-button" style="margin-top: 20px;" primary/>
		</div>
	</div>
</template>

<script>
	export default{
		data() {
			return {
				project: "数据库原理与应用",
				time: 7200,
				paperId: 0,
				testTitle: "如果你无法简洁的表达你的想法，那只说明你还不够了解它,\
				如果你无法简洁的表达你的想法，那只说明你还不够了解它。-- 阿尔伯特·爱因斯坦",
				testSelectA: "如果你无法简洁的表达你的想法",
				testSelectB: "如果你无法简洁的表达你的想法",
				testSelectC: "如果你无法简洁的表达你的想法",
				testSelectD: "如果你无法简洁的表达你的想法",
				titleList: [{
					testTitle: "如果你无法简洁的表达你的想法，那只说明\
					你还不够了解它,如果你无法简洁的表达你的想法，那只说明你还不够了解它。-- 阿尔伯特·爱因斯坦",
					testSelectA: "如果你无法简洁的表达你的想法",
					testSelectB: "如果你无法简洁的表达你的想法",
					testSelectC: "如果你无法简洁的表达你的想法",
					testSelectD: "如果你无法简洁的表达你的想法",
					testType: 1,
					questionNumber: 25
				}],
				state: [],
				answer: [],
				judgeVal: '',
				answerList: [],
				nowIndex: 0,
				judgeDisplay: 'none',
				selctDisplay: 'block'
			}
		},
		mounted: function(){
			this.$ajax({
				method: 'get',
				url: './static/mock/doTest.json',
			})
			.then((response) => {
				var data = response.data.data
				this.project = data.projectName
				for(var i = 0; i < data.titleList.length; i++){
					this.state[i] = '未操作'
					this.answerList[i] = []
				}
				this.titleList = data.titleList
				console.log(this.titleList)
				this.changeShow(0)
				this.calTime()
			})

		},
		watch: {
			answer: function(val){
				//answe值改变更新列表
				if(!val.length){
					this.state[this.nowIndex] = '未操作'
					this.answerList[this.nowIndex] = []
				}else{
					this.state[this.nowIndex] = val
					this.answerList[this.nowIndex] = val
				}
			},
			judgeVal: function(val){
				if(!val){
					this.state[this.nowIndex] = '未操作'
					this.answerList[this.nowIndex] = []
				}else{
					this.state[this.nowIndex] = val
					this.answerList[this.nowIndex][0] = val
				}
			}
		},
		methods: {
			changeShow(index){
				//改变内容显示
				if(index < this.titleList.length){
					this.testTitle = this.titleList[index].testTitle
					this.nowIndex = index
					//将现在的索引变为操作的值
					if(this.titleList[index].testType){
						//题型为选择题
						this.answer = this.answerList[index]
						this.testSelectA = this.titleList[index].testSelectA
						this.testSelectB = this.titleList[index].testSelectB
						this.testSelectC = this.titleList[index].testSelectC
						this.testSelectD = this.titleList[index].testSelectD
						this.judgeDisplay = 'none'
						this.selctDisplay = 'block'
					}else if(this.titleList[index].testType == 0){
						//判断题
						this.judgeVal = this.answerList[index][0]
						this.judgeDisplay = 'block'
						this.selctDisplay = 'none'
					}
				}else{
					alert('已到最后一题')
				}
				
			},
			calTime(){
				//倒计时函数
				var _this = this
				var timer = setInterval(function(){
					_this.time--
					if(_this.time <= 0){
						clearInterval(timer)
						_this.sendAnswer()
						
					}
				}, 1000)
			},
			sendAnswer(){
				//发送请求
				var _this = this
				this.$ajax({
	        method: 'get',
	        url: '',	
	        params: {
	          paperId: _this.paperId,
	          answerList: _this.answerList
	        }
	      })
	      .then((response) => {
	        alert('考试结束')
	      })
			}
		}
	}
</script>

<style>
	.testList{
		width: 30%;
		float: left;
	}
	.testContant{
		width: 68%;
		height: 500px;
		float: right;
		border-left: 0.5px solid #DDD;
	}
	.testList .mu-item{
		border-bottom: 0.5px solid #DDD;	
	}
	.testList .mu-item-title{
		font-size: 14px;
		text-align: left;
	}
	.testTitle{
		padding: 10px;
		margin-top: 6px;
		text-align: left;
		margin-bottom: 20px;
	}
	.testOptions p{
		padding: 10px;
		margin-bottom: 16px;
		margin-top: 10px;
	}
	.project{
		padding: 10px;
		display: inline-block;
	}
	.time{
		padding: 10px;
		display: inline-block;
	}
	.selct p{
		text-align: left;
		margin-left: 50px;
	}
</style>


