<template>
<div>
  <mu-select-field v-model="game1" :labelFocusClass="['label-foucs']" label="选择添加科目">
    <mu-menu-item v-for="text,index in list" :key="index" :value="index" :title="text" />
  </mu-select-field>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th tooltip="试题编号">试题编号</mu-th>
        <mu-th tooltip="试题类型">试题类型</mu-th>
        <mu-th>操作</mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.topicId}}</mu-td>
        <mu-td>{{item.topicType}}</mu-td>
        <mu-td @click="clickList(index)">
          <mu-dropDown-menu :value="value" @change="handleChange">
            <mu-menu-item value="1" title="编辑"/>
            <mu-menu-item value="2" title="添加"/>
            <mu-menu-item value="3" title="删除"/>
          </mu-dropDown-menu>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
</div>
</template>

<script>
export default {
  data () {
    return {
      game1: 0,
      list: ['计算机组成原理', '数据库', '算法设计与分析', '无线传感器', '计算机操作系统', '软件工程'],
      tableData: [
        {
          topicId: '1505120206',
          topicType: '单选'
        },
        {
          topicId: '1505120206',
          topicType: '多选'
        },
        {
          topicId: '1505120206',
          topicType: '判断'
        }
      ],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      value: '1',
      index: 0
    }
  },
  mounted: function(){
    this.$ajax({
      method: 'get',
      url: './static/mock/projectInfo.json',
      params: {
        typeNumber: this.game1,
        typeName: this.list[this.game1]
      }
    })
    .then((response) => {
      console.log(response)
      this.tableData = response.data.tableData
    })
  },
  methods: {
    clickList (index) {
      //取到当前列
      this.index = index
    },
    handleChange (value) {
      this.value = value
      console.log(value)
      console.log(this.index)

    }
  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


