<template>
  <div class="layout">
    <div class="header">
      <div class="logo">
        溜飞天在线考试网
      </div>
    </div>
    <div class="content">
      <div class="body">
        <div style="padding-top:80px;">
          <mu-text-field label="考生号" labelFloat/><br/>
          <mu-text-field label="密码" hintText="请输入密码" type="password" labelFloat/><br/>
          <mu-radio label="管理登陆" name="group" nativeValue="1" v-model="selectVal" class="demo-radio"/> <br/>
          <mu-radio label="考生登陆" name="group" nativeValue="0" v-model="selectVal"  class="demo-radio"/> <br/>
          <mu-raised-button label="登 陆" class="demo-raised-button" primary style="margin-top: 40px;" @click="login" :to="link"/>
        </div>
      </div>
    </div>
    <div class="footer">
      数据库课设 ©2017 Created by Muse-UI
    </div>

  </div>
</template>
<script>
export default {
  data () {
    return {
      activeTab: 'tab1',
      userId: '1505120515',
      userName: '汤姆',
      sex: '男',
      selectVal: '0',
      link: '/front/regular'
    }
  },
  watch: {
    selectVal: function(val){
      //不同类型登陆选择 
      if(val == '1'){
        this.link = 'back/studentInfo'
      }
      if(val == '0'){
        this.link = '/front/regular'
      }
    }
  },
  methods: {
    login() {
      //登陆
      if(this.selectVal == '1'){
        //管理员登陆
        this.sessionSetter('login', 'manage')
      }else{
        //考生登陆
        this.sessionSetter('login', 'user')
      }
    },
    handleTabChange (val) {
      this.activeTab = val
    },
    sessionGetter(key){
      //取缓存内容
      const prefix = 'html5_test_'
      return sessionStorage.getItem(prefix + key);
    },
    sessionSetter(key, val){
      //设置缓存
      const prefix = 'html5_test_'
      sessionStorage.setItem(prefix + key, val)
    }
  }
}
</script>
<style scoped>
.layout{
  background-color: rgb(236, 236, 236);
}

.header{
  background-color: #7e57c2;
}

.logo{
  font-size: 24px;
  color: white;
  display: inline-block;
  padding: 10px 20px;
}

.nav{
  display: inline-block;
  width: calc(100% - 150px);
  margin: 0 auto;
}

.tab{
  margin: 0 auto;
  width: 400px;
  background-color: rgba(0, 0, 0, 0);
}

.content{
  width: 90%;
  margin: 0 auto;
}

.breadcrumb{
  margin: 10px 0;
}

.body{
  background-color: white;
  border-radius: 5px;
  min-height: 500px;
}

.footer{
  padding: 20px 0;
  text-align: center;
}
</style>


